body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
  background-image: -webkit-linear-gradient(left bottom, #212b2e, #32383b, #424548, #535355, #626262);
  background-image: linear-gradient(to right top, #212b2e, #32383b, #424548, #535355, #626262);
}

.heading {
  margin: 0;
  padding: 4%;
}

.images {
  display: -webkit-flex;
  display: flex;
  max-width: 80%;
  margin: 2% auto;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}
.single-photo {
  max-width: 300px;
  -webkit-align-self: auto;
          align-self: auto;
  margin: 1% 2px;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  vertical-align: middle;
}

